header {
  display: flex;
  width: 100%;
  padding: 2rem;
  justify-content: space-between;
  align-items: center;
  height: 105px;
}
.logo {
  width: 50%;
  display: flex;
  text-align: center;
  align-items: center;
}
.strong p {
  max-width: 300px;
  font-weight: 700;
  font-size: 2.5rem;
  line-height: 3rem;
  text-transform: uppercase;
  color: var(--color-blue);
}
.logoImg {
  width: 65px;
}

.navigation {
  width: 50%;
  display: flex;
  text-align: center;
}
.navigation_teme {
  justify-content: end;
}
.navigation_error {
  justify-content: end;
}

button {
  background: white;
  margin: 0 25px;
  width: 28px;
  height: 28px;
}
@media (min-width: 1024px) {
  header {
    min-width: 960px;
  }
}
@media (max-width: 1024px) {
}

@media (max-width: 480px) {
  .strong p {
    font-weight: 700;
    font-size: 2.1rem;
    line-height: 2rem;
    text-transform: uppercase;
    color: var(--color-blue);
  }
}
